.field{
    border: 1px solid black;
}
.fieldWrapper{
    display: flex;
    justify-content: center;
    align-content: center;
    width: 100vw;
}
.fieldLine {
    display: flex;
}


.stoneFrame{
    border: 1px solid black;
}
.stoneFrame div{
    width: 90%;
    height: 90%;
    margin: 5%;
    position: relative;
}


.stoneElement{
    background-size: 100% !important;
    border-radius: 50%;
}
.stoneElement span{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
}


.infoStone{
    width: 30px;
    height: 30px;
    display: inline-block;
    transform: translateY(10px);
}
